.ape-page{
    box-sizing: border-box;
    font-family:'lato', sans-serif;
    margin-top: 100px;
    padding: 0px 12px;
    margin-left: auto;
    margin-right: auto;
}
.ape-page h1{
    font-size: (1.35rem+1.5vw);
}
.ape-page h3{
    font-size: (1.3rem+1.5vw);
}
.list{
    text-align: left;
}
ul{
    list-style-type: disc;
}
.versions{
    margin-left: 5%;
    float: left;
}
.versions label {
    margin-right: 20px;   
}
.versions option {
    color: white;
}
.versions select{
    outline: none;
    color: white;
    height: 30px;
    width: 288px;
    background-color: #303032;
    border: 1px solid rgb(81, 81, 255);
    border-radius: 4px;
    box-shadow: 0 0 5px 2px white;
    margin-bottom: 16px;
}
.ape-page button{
    border: none;
    color:black;
    background-color: rgb(26, 207, 26);
    outline: none;
    height: 30px;
    width: 50px;
    border-radius: 5px;
    cursor: pointer;
}