
.buycoin-page{
    font-family:'lato', sans-serif;
    margin-top: 100px;
}
.coinbuy{
    display: flex;
    justify-content: center;
   
}
.coinbuy button{
    margin: 15px 0 0 48px;
    height: 43px;
    width: 68px;
    font-size: 20px;
    color: black;
    background-color: rgb(47, 209, 47);
    outline: none;
    cursor: pointer;
    border: none;
    border-radius: 5px;

}


.pricewithlogo{
    margin-left: auto;
    margin-right: auto;
    height: 80px;
    width: 268px;
    background-color: #262626;
    border-radius: 45px;
    display: flex;
    box-sizing: border-box;
    padding: 10px;
    border: 2px solid white;
    margin-bottom: 16px;
}
.whitebg{
    background-color: white;
    padding: 5px;
    border-radius: 50%;
    margin-left: 5%;
}
.price{
    padding: 10px;
    font-size: 30px;
    margin-left: 5%;
    }

.links a{
   color: #0d6efd;
   cursor: pointer;
   font-size: 27px;
   
}
/* For mobile screens */
@media screen and (max-width:800px) {
    .buycoin-page{
        margin-top: 180px;
        margin-left: 2%;
        margin-right: 2%;
        /* display: flex;
        flex-wrap: wrap; */
    }    
  }