
.promote-page{
    font-family:'lato', sans-serif;
    margin-top: 100px;
}
.promote-page h1{
    font-size: 37px;
    margin-top: 25px;
}
.alert{
    color: #842029;
    background-color: #f8d7da;
    height: 58px;
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    margin-bottom: 16px;
}
.disablecheck{
    border-radius: 5px;
    margin-right: auto;
    margin-left: auto;
    background-color: #262626;
    color: #f8f9fa;
    height: 58px;
    width: 90%;
    display: flex;
    justify-content:center;
    align-items: center;
}
.disablecheck input{
    width: 15px;
    height: 15px;
    margin: 10px;
    outline: none;
    border: 1px solid black;
    cursor: pointer;
}

.promote-nav{
    height: auto;
    background-color: #303032;
    width: 90%;
    display: flex;
    justify-content: start;
    align-items: center;
    margin-right: auto;
    margin-left: auto;
    position: relative;
}
.promote-nav a{
    border: 1px solid white;
    padding: 4px 8px ;
    background-color: rgb(41, 40, 40);
    border-radius: 5px;
    display: flex;
    flex-wrap: wrap;
    /* position: absolute; */
    

}


/* For mobile screens */
@media screen and (max-width:800px) {
    .promote-page{
        margin-top: 180px;
        margin-left: 2%;
        margin-right: 2%;
    }    
    .promote-nav{
        display: flex;
        flex-wrap: wrap;
    
    
    }
    .promote-nav a{
        margin: 5px;
    }
}