.tool-toilet-page{
    box-sizing: border-box;
    font-family:'lato', sans-serif;
    margin-top: 100px;
    padding: 0px 12px;
    margin-left: auto;
    margin-right: auto;
}
.tool-toilet-page h1{
    font-size: (1.35rem+1.5vw);
}
.tool-toilet-page h3{
    font-family:'lato', sans-serif;
    font-size: (1.3rem+1.5vw);
}
.tool-toilet-page button{
    padding: 6px 12px;
    height: 30px;
    color: #ffff;
    background-color: #262626;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
    border: 1px solid white;
}
.tool-toilet-page button:hover{
    border-top: 1px solide rgb(143, 143, 143);
}