.header{
    box-sizing: border-box;
    color:White;
    background-color: #262626;
    display: flex;
    flex-wrap: wrap;
    /* justify-content:space-between ; */
    border-bottom:3px solid #484e53;
    padding: 5px 2%;
    width: 100%;
    box-shadow: 0 .125rem .25rem rgba(0,0,0,.075);
    overflow: hidden;
    position: fixed; 
    top: 0;
    width: 100%; 
    margin-bottom: 100px;
    z-index: 10;    
}
/* Logo Portion is Styled here */
.logo-portion{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}
.poocoinlogo{
    padding: 10px;
}
.poocoin-text{
    font-weight: bold;
    font-size: .875rem;
    box-sizing: border-box;
    font-family: "Lato",sans-serif;
}
.poocoin_price{
    margin-left: 50px;
    margin-right: 10px;
    display: flex;
    align-items: center;  
    justify-content:center;     
}
.poocoin_price a{
    color: rgb(81, 247, 81);
    display: flex;
    justify-content: center;
    align-items: center;
}
.logowithwhiteBackground{
    border-radius: 50%;
    margin-right: 10px;
    background-color: white;
}
a{
    color: white;
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}

/* Rest of the nav bar styling added here */
.navbar{
    color: #f8f9fa;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content:center;
    z-index: 4;
}
.item-active{
    color: #6c757d;
}
.navbar-ul{
    z-index: 14;
    display: flex;
    align-items: center;
    justify-content:center;
    list-style:none;
}
li{
    text-decoration: none;
    font-family: "Lato",sans-serif;
    margin: 0 10px;
}


.connect button{
    padding: 4px 8px;
    cursor: pointer;
    background: #f8f9fa;
    outline: none;
    height: 25px;
    width: 70px;
    border: none;
    border-radius: 5px;
    color: black;
    cursor: pointer;
    font-family: "Lato",sans-serif;
    position: relative;       
    margin-top: 5px;
    left: 10vw;   
}

/* In this portion mobile view is styled */
@media only screen and (max-width: 500px) {
    .header {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        position: fixed;   
        overflow:none;
        top: 0;
        width: 100%;    
    
    }
    .navbar-ul{
        display: flex;
        align-items: center;
        justify-content:center;
        flex-wrap: wrap;
        list-style:none;    
        line-height: 30px;
        margin-right:70px;
    }
    .connect button{      
        display: flex;
        flex-wrap: wrap;
       
    }
    .logo-portion{
        margin-right: 50px;
    }
  }