.homepage{
    box-sizing: border-box;
    font-family: "Lato",sans-serif;
    margin-top: 90px;
    display:block;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    justify-content: center;
    color: #f8f9fa;
    background-color: #262626;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0,0,0,0);   
}
h1{
    font-size: calc(1.375rem + 1.5vw);    
}
h2{
    margin-top: 0;
    font-size: calc(1.325rem + .9vw);
    box-sizing: border-box;
    padding: 2%;
}

.searchTokens{
    box-sizing: border-box;
    display: block;
    margin-bottom: 1rem;
    padding: 4px 8px;
}
.searchTokens input{
    padding: 0 5px;
    width: 450px;
    height: 31px;
    outline: none;
    border: none;
    border-radius: 3px 0  0 3px;
    
}
.searchTokens button{
    border-radius:0  3px  3px 0;
    width: 46px;
    height: 31.5px;
    outline: none;
    border: none;  
    background-color:green; 
    cursor: pointer;
}


/* For mobile screens */

@media screen and (max-width:800px) {
 .homepage{
     box-sizing: border-box;
     display: flex;
     flex-wrap: wrap;
     justify-content: center;
     align-items: center;
     padding: 0 5%;
 }    
 h2{
    font-size: calc(1.325rem + .9vw);
    margin-top: 0;
    margin-bottom: .5rem;
    font-weight: 500;
    line-height: 1.2;
 }
 .searchTokens{
     display: flex;
 }
.searchTokens input{
    width: 80vw;
}
}

