.premium-page{
    padding: 0;
    font-size: 37.5px;  
    margin-right: auto;
    margin-left: auto;
    font-family:'lato', sans-serif;
    color: #f8f9fa;
    margin-top: 100px;
}
.premium-page p{
    font-size: 16px;
    margin-bottom: 16px;
}
.premium-page span {
    color: #28a745;
}
.premium-page a{
    color: #0d6efd;
}

.features{
    background-color: #303032;
    margin: 16px 0;
    border-radius: 5px;
    width: 95%;  margin-right: auto;
    margin-left: auto;
}
.features h3{
    font-family:'lato', sans-serif;
    font-size: 27px;
}
.features p{
    font-family:'lato', sans-serif;
    font-size: 16px;
    margin-bottom: 8px;
}
.features input{    
        width: 15px;
        height: 15px;
        margin: 10px;
        outline: none;
        border: 1px solid black;
        cursor: pointer;
}
.features label{
    font-size: 16px;

}


/* For mobile screens */
@media screen and (max-width:800px) {
    .premium-page{
        margin-top: 180px;
        margin-left: 2%;
        margin-right: 2%;
    }    
}