
.unvettedlist{
    color: #f8f9fa;
    margin-top: 150px;
    font-family:'lato', sans-serif;
   box-sizing: border-box;
   margin-right:auto;
   margin-left: auto;
   background-color: #353333;
    width: 90%;
    text-align-last: left;
    padding: 5px;
    box-shadow: 0 0 5px 5px rgb(85, 83, 83);
}
.unvettedlist h3{
    font-size: 28px;
    font-family:'lato', sans-serif;
    margin-bottom: 10px;
}
.unvettedlist p{
    font-family:'lato', sans-serif;
    font-size: 16px;
}
.unvettedlist p{
    font-family:'lato', sans-serif;
    text-align: left;
    margin-bottom: 16px;
}
.tokenaddress{
    font-family:'lato', sans-serif;
    border-radius: 5px;
}
.unvettedlist input{
    height: 30px;
    width: 300px;
    outline: none;
    border: none;
    margin-bottom: 10px;
    border-radius: 3px ;
    margin-left: 2px;
}
.unvettedlist span{
    font-size: 14px;
}
.tokenaddress label{
    font-size: 16px;
}
.warning h5{
   color:  #f7b500;
}

.add_input{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.input_block{
    display: block;
    position: relative
}

/* For mobile screens */
@media screen and (max-width:800px) {
    .unvettedlist{
        margin-top: 180px;       
    }    
